import React, {useState} from "react";
import './PhotoGaleria.css';
import ImagePopup from '../ImagePopup/ImagePopup';

function PhotoGaleria(props) {
  const [openImagePopup, setOpenImagePopup]=useState(false);
  const [photo, setPhoto]=useState({});
  const [form, setForm] =React.useState(true);

  const openImage=(item)=>{
    setOpenImagePopup(true);
    setPhoto(item);
    setForm(true);
  }

  const closePopup=()=>{
    setForm(false);
    }
  return (<>
          <div className="photos" >
                <div className="photos__title" > {props.title} </div>
                    <div className="photos__container" > 
                    {props.photo.map(item => (
                        <img className="photo_img" src={item} onClick={()=>openImage(item)}  />
                    ))}                  
                </div>  
          </div>
          {openImagePopup&& <ImagePopup photo={photo} onClose={closePopup} form={form}/>}
          </>
  );
}

export default PhotoGaleria;