import React from 'react';
import './ImagePopup.css';


function ImagePopup({photo, onClose, form}) {

  const handleClose=()=>{
    onClose();
  }

  return (
    <div className={`popup popup_type_img ${form ? "popup_opened" : ""}`}>
      <div id="imagePopup" className="popup__img">
        <button type="button" className="popup__close popup__close_type_img" onClick={handleClose}>
          <div className="popup__close-icon"></div>
        </button>
        <img className="popup__image" src={photo} alt='image' />
      </div>
    </div>
  );
}

export default ImagePopup;